html {
  height: 100%;
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  height: 100%;
  font-family: 'Inter', sans-serif;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  min-height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #FEFCF7;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow: hidden;
  &2 {
    background-color: transparent;
  }
}
.content {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
}
.container-fluid {
  max-width: 1815px;
}
img {
  object-fit: cover;
}

@media screen and (max-width: 576px) {
  body {
    background: #FFF;
  }
}

@media screen and (max-width: 359px) {
  .container-fluid {
    padding: 0 1.5rem;
  }
}