.footer {
  position: relative;
  z-index: 5;
  background-image: url("../img/footer-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #F0F0F0;
  &2 {
    background-image: unset;
  }
}
.footer-top {
  padding: 8.125rem 0 3.125rem;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    max-width: 17.8125rem;
    margin-right: 3rem;
  }
  &__right {
    max-width: 67.5rem;
    width: 100%;
  }
  &__logo {
    width: 13.4375rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.1875rem;
    margin-top: 2.1875rem;
    p {
      font-size: 0.875rem;
      line-height: 130%;
      letter-spacing: -0.149px;
      margin: 0;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 3.125rem;
  }
  &__item {
    &:nth-child(3) {
      max-width: 15.9375rem;
    }
    &-title {
      font-size: 1.875rem;
      line-height: 130%;
      font-weight: 700;
      letter-spacing: 0.6px;
      margin-bottom: 1.875rem;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25rem;
    }
    ul li {
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 500;
      &:hover {
        a {
          color: #FDC32E;
        }
      }
      .active {
        color: #FDC32E;
        text-decoration: underline;
        text-underline-offset: 0.3125rem;
      }
    }
  }
  &__wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875rem;
  }
  &__group {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
  }
  &__text2 {
    font-size: 1.125rem;
    line-height: 130%;
    letter-spacing: 0.36px;
  }
  &__subtitle {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 0.32px;
  }
  &__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 0.4px;
    &:hover {
      color: #FDC32E;
    }
  }
  &__address {
    font-size: 1rem;
    line-height: 130%;
    letter-spacing: 0.32px;
  }
  &__email {
    font-size: 1rem;
    line-height: 130%;
    letter-spacing: 0.32px;
    color: #FDC32E !important;
  }
}
.footer-bottom {
  background: linear-gradient(180deg, rgba(32, 50, 62, 0.80) 0%, rgba(40, 58, 69, 0.00) 100%);
  padding: 1.875rem 0;
  font-size: 0.75rem;
  line-height: 130%;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem 2.5rem;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 12.5rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__copyright {
    margin-left: 2.5rem;
  }
  &__text {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-left: 2.5rem;
    p {
      margin: 0;
    }
  }
  &__politic {
    color: #FDC32E;
    text-decoration: underline;
    text-underline-offset: 0.3125rem;
    margin-left: 2.5rem;
  }
}


@media screen and (max-width: 1600px) {
  .footer-top {
    &__left {
      margin-right: 2rem;
    }
    &__items {
      gap: 2rem;
    }
    &__item {
      &-title {
        font-size: 1.5rem;
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .footer-top {
    padding-top: 6.25rem;
    &__inner {
      display: block;
    }
    &__left {
      margin: 0 0 2.5rem 0;
    }
    &__right {
      max-width: 100%;
    }
  }
  .footer-bottom {
    &__inner {
      gap: 1rem 1.5rem;
    }
    &__logo {
      width: 10rem;
    }
    &__copyright {
      margin-left: 1.5rem;
    }
    &__text {
      margin-left: 1.5rem;
    }
    &__politic {
      margin-left: 1.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    &__text {
      gap: 1.25rem;
      margin-top: 1.25rem;
    }
    &__item {
      &-title {
        font-size: 1.25rem;
        margin-bottom: 1.25rem;
      }
      ul {
        gap: 0.625rem;
      }
      ul li {
        font-size: 1rem;
      }
    }
    &__wrap {
      gap: 1.25rem;
    }
    &__text2 {
      font-size: 1rem;
    }
  }
  .footer-bottom {
    &__copyright {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    padding: 3.125rem 0 1.875rem;
    &__items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 2.5rem / 2));
      gap: 2.5rem;
    }
  }
  .footer-bottom {
    padding: 1rem 0;
    &__inner {
      display: block;
    }
    &__left {
      display: block;
    }
    &__right {
      display: block;
      margin: 0.625rem 0 0 0;
    }
    &__text {
      flex-wrap: wrap;
      gap: 0.75rem;
      margin: 1.25rem 0 0 0;
    }
    &__politic {
      display: block;
      margin: 0.625rem 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__left {
      margin-bottom: 2rem;
    }
    &__logo {
      width: 10.3125rem;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 2rem;
    }
    &__item {
      &-title {
        font-size: 1.125rem;
      }
      ul li {
        font-size: 0.875rem;
      }
    }
    &__address {
      font-size: 0.875rem;
    }
    &__email {
      font-size: 0.875rem;
    }
  }
}

